'use strict';

// MAIN //

/**
* Sets an array element.
*
* @private
* @param {Collection} x - input array
* @param {NonNegativeInteger} idx - element index
* @param {*} value - value to set
*/
function setter( x, idx, value ) {
	x[ idx ] = value;
}


// EXPORTS //

export default setter;
